import React from "react";
import { useState } from "react";

export default function Game() {

  const [history,setHistory] = useState(emptyGameHistory());
  const [currentMove,setCurrentMove] = useState(0);
  const [ascending,setAscending] = useState(true);
  const [locations,setLocations] = useState([]);
  const squares = history[currentMove];
  const player = (currentMove % 2 === 0) ? "X" : "O";
  const nextOrderLabel = (ascending) ? "Descending" : "Ascending";
  
  function handlePlay(newSquares,newLocations) {
    const historySlice = history.slice(0,currentMove+1);
    const updatedHistory = addElement(historySlice,newSquares);
    setHistory(updatedHistory);
    setLocations(newLocations.slice(0,currentMove+1));
    setCurrentMove(updatedHistory.length-1);
  }
  
  function getHistoryContent(index,currentMove) {
    const location = (index > 0) ? locations[index-1] : null;
    if (index === currentMove) {
      return getCurrentMoveLabel(index,location);
    } else {
      const label = getJumpToLabel(index,location);
      return <button onClick={() => setCurrentMove(index)}>{label}</button>
    }
  }

  function getHistoryElement(index,currentMove) {
    const content = getHistoryContent(index,currentMove);
    return <li key={index}>{content}</li>
  }
  
  const elements = history.map((squares,index) => getHistoryElement(index,currentMove));
  const sorted = (ascending) ? elements : elements.reverse();
  const reversed = (ascending) ? "" : "reversed";
  const start = (ascending) ? 0 : (sorted.length - 1);
  
  return (
    <div>
      <div className="game">
        <div className="game-board">
          <Board player={player} squares={squares} locations={locations} onPlay={handlePlay}/>
        </div>
        <div className="game-info">
          <div className="game-history-order">
            <button onClick={() => setAscending(!ascending)}>{nextOrderLabel}</button>
          </div>
          <ol reversed={reversed} start={start}>{sorted}</ol>
        </div>
      </div>
    </div>
  );
}


function Board({player,squares,locations,onPlay}) {
  
  function handleClick(index) {
    if (squares[index] || getWinner(squares)) {
      return;
    }
    const newSquares = squares.slice();
    newSquares[index] = player;
    const location = getLocation(index);
    const newLocations = addElement(locations.slice(),location);
    onPlay(newSquares,newLocations);
  }
  
  const winner = getWinner(squares);
  const finished = getFullBoard(squares);
  const status = getStatusLabel(winner,finished,player);
  const winningSquares = getWinningSquares(squares);

  function getRow(sequence) {
    const start = (sequence - 1) * 3;
    const row = [];
    for (let i=0;i<3;i++) {
      const index = start + i;
      const winner = isWinner(index,winningSquares);
      row[i] = <Square key={index} value={squares[index]} winner={winner} onSquareClick={() => handleClick(index)}/>;
    }
    return row;
  }
  
  function getBoard() {
    const board = [];
    for (let i=0;i<3;i++) {
      const sequence = i + 1;
      board[i] = <div key={sequence} className="board-row">{getRow(sequence)}</div>;
    }
    return board;
  }
  
  function getStatus(label) {
    return <div className="status">{status}</div>;
  }
  
  return (<div>{getBoard()}{getStatus(status)}</div>);
}

function Square({value,winner,onSquareClick}) {
  const bgcolor = (winner) ? "#ccc" : "#fff";
  return <button className="square" style={{backgroundColor:bgcolor}} onClick={onSquareClick}>{value}</button>;
}

// determine if the board has a winner
// if so, return who won, either X or O
function getWinner(squares) {
  // every possible winning combination for tic-tac-toe
  const wins = [
    [0,1,2],
    [3,4,5],
    [6,7,8],
    [0,3,6],
    [1,4,7],
    [2,5,8],
    [0,4,8],
    [2,4,6]
  ];
  // iterate across all of the winning combinations
  for (let i=0;i<wins.length;i++) {
    // extract this potentially winning combination
    const [a,b,c] = wins[i];
    // check to see if the first square from this winning combination has a value
    // check to see if the other two squares have the same value as the first square
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      // if so, we have a winner :)
      return squares[a];
    }
  }
  // no winner yet
  return null;
}

// return true if every square has a mark in it, false otherwise
function getFullBoard(squares) {
  if (squares.length === 0) {
    return false;
  }
  for (let i=0;i<squares.length;i++) {
    if (!squares[i]) {
      // there is at least one empty square
      return false;
    }
  }
  // all squares have a mark
  return true;
}

function getStatusLabel(winner,finished,player) {
  if (winner) {
    return "'" + winner + "' won";
  }
  if (finished) {
    return "-- Tie Game --";
  }
  return "'" + player + "' is next";
}

function addElement(oldArray,element) {
  const newArray = [];
  for (let i=0;i<oldArray.length;i++) {
    newArray[i] = oldArray[i];
  }
  newArray[newArray.length] = element;
  return newArray;
}

function emptyGameHistory() {
  const squares = [];
  const history = [];
  return addElement(history,squares);
}

function getCurrentMoveLabel(index,location) {
  if (index === 0) {
    return "You are at game start";
  } else {
    const suffix = getLocationLabel(location);
    return "You are on move # " + index + " " + suffix;
  }
}

function getLocationLabel(location) {
  return "(" + location.row + "," + location.column + ")";
}

function getJumpToLabel(index,location) {
  if (index === 0) {
    return "Start of game";
  } else {
    const suffix = getLocationLabel(location);
    return "Go to move # " + index + " " + suffix;
  }
}

function getWinningSquares(squares) {
  // every possible winning combination for tic-tac-toe
  const wins = [
    [0,1,2],
    [3,4,5],
    [6,7,8],
    [0,3,6],
    [1,4,7],
    [2,5,8],
    [0,4,8],
    [2,4,6]
  ];
  // iterate across all of the winning combinations
  for (let i=0;i<wins.length;i++) {
    // extract this potentially winning combination
    const [a,b,c] = wins[i];
    // check to see if the first square from this winning combination has a value
    // check to see if the other two squares have the same value as the first square
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      // if so, we have a winner :)
      return wins[i];
    }
  }
  // no winner yet
  return null;
}

function isWinner(index,winningSquares) {
  if (!winningSquares) {
    return false;
  }
  for (let i=0;i<winningSquares.length;i++) {
    if (index === winningSquares[i]) {
      return true;
    }
  }
  return false;
}

function Location(row,column) {
  this.row = row;
  this.column = column;
}

function getLocation(index) {
  const row = getSquareRow(index);
  const column = getSquareColumn(index);
  return new Location(row,column);
}

function getSquareRow(index) {
  return Math.floor(index/3) + 1;
}

function getSquareColumn(index) {
  const row = getSquareRow(index);
  const rowIndex = (row - 1);
  const subtract = (rowIndex * 3);
  return (index - subtract) + 1;
}


